@import url('https://fonts.googleapis.com/css2?family=Diphylleia:wght@400&display=swap');

h1, body, html {
    font-family: 'Diphylleia', sans-serif;
    -moz-osx-font-smoothing: grayscale;
    -webkit-font-smoothing: antialiased;
}

.intro-section {
        position: relative;
        display: flex;
        align-items: center;
        justify-content: center;
        height: 100vh;
        padding: 1.5rem;
        overflow: hidden; /* Hide the overflow */
}

.intro-section::before {
        content: '';
        position: absolute;
        top: 75%;
        right: 0;
        bottom: 0; /* This will make the height 25% */
        left: 0;
        background-image: url('../../assets/poppy.jpg');
        background-size: cover;
        background-repeat: no-repeat;
        background-position: center;
        /*z-index: -1; */
}

.intro-title {
        /* display: flex; */
        flex: 1;
        font-size: 2.5rem;
        text-align: center;
        /*color: white;*/
